.Header {
  min-height: 500px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;

  &__menu-container {
    max-width: 1400px;
  }

  &__top-menu-container {
    max-width: 1400px;
    display: flex;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    justify-content: space-between;
  }

  &__top-menu-element {
    display: flex;
  }

  &__menu {
    background-color: #ffffff;
    max-width: 1400px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
  }

  &__top-menu {
    background-color: #eff0f0;
  }

  &__title-whatsapp {
    color: #737373;
    position: relative;
    cursor: pointer;
  }

  &__title-whatsapp::before {
    content: url("../../images/Whatsapp.svg");

    left: -24px;
    position: absolute;
    width: 30px;
    text-align: left;
  }

  &__title-whatsapp:hover:before {
    content: url("../../images/gren-WA.svg");
  }

  &__title-time {
    color: #737373;
    position: relative;
  }

  &__title-time::before {
    content: url("../../images/clock.svg");

    left: -24px;
    position: absolute;
    width: 30px;
    text-align: left;
  }

  &__title-map {
    color: #737373;
    position: relative;
    margin-left: 25px;
  }

  &__title-map::before {
    content: url("../../images/map.svg");

    left: -24px;
    position: absolute;
    width: 30px;
    text-align: left;
  }

  &__menu-pages {
    display: flex;
    justify-content: space-between;
  }

  &__menu-select {
    display: flex;
  }

  &__headers {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: left;
    max-width: 205px;
  }

  &__link {
    text-decoration: none;
    color: #262626;
    align-self: center;
    margin-right: 16px;
  }
  &__link:hover {
    color: #ca0100;
  }

  &__phone {
    color: #737373;
    position: relative;
    cursor: pointer;
    margin-left: 50px;
  }

  &__phone::before {
    content: url("../../images/phone.svg");
    left: -36px;
    top: -6px;
    position: absolute;
    text-align: left;
  }
}
